import React, { Component, useEffect } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect
} from "react-router-dom";

import Home from '../Pages/Home'
import Post from '../Pages/Post'
import Menu from '../Pages/Menu'
import PostDetail from '../Pages/PostDetail'

class AppRoute extends Component {
  render() {
    return (
      <Router>
        <div>
          <Switch>
            <Route path="/home">
              <Home />
            </Route>
            <Route
              path="/post"
              render={({ match: { url } }) => (
                <Switch>
                  <Route path={`${url}/`} component={Post} exact />
                  <Route path={`${url}/:url`} component={PostDetail} />                                   
                  <Route render={() => <Redirect to="/tools" />} />
                </Switch>
              )}
            />
            <Route path="/menu">
              <Menu />
            </Route>
            <Route path="/about">
              <Home />
            </Route>
            {/* <Route
              path="/tools"
              render={({ match: { url } }) => (
                <Switch>
                  <Route path={`${url}/`} component={Tools} exact />
                  <Route path={`${url}/instagrampost`} component={InstagramPost} />
                  <Route path={`${url}/instagramstory`} component={InstagramStory} />
                  <Route path={`${url}/youtubevideo`} component={youtubeVideo} />                  
                  <Route render={() => <Redirect to="/tools" />} />
                </Switch>
              )}
            /> */}
            <Route path="/" exact>
            <Home />
            </Route>
          </Switch>
        </div>
      </Router>
    );
  }
}

export default AppRoute;
