import { combineReducers } from 'redux'
import * as Type from '../Type'

const intialState = {
    isSideMenuOpen : false,
    instagram: 'hikhtiarta',
    facebook: 'hasan.ikhtiarta',
    appName: 'Kopistasi',   
    isLoading: false, 
}

const appReducer = (state = intialState, action) => {
    switch (action.type) {
        case Type.openSideMenu : return {
            ...state,
            isSideMenuOpen: action.payload
        }  
        case Type.setLoading : return {
            ...state,
            isLoading: action.payload
        }        
        default:
            return state
    }
}

export default combineReducers({
    app: appReducer
})