import React from 'react';
import * as Styles from './Styled'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid';

//redux
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux'
import { openSideMenu } from '../config/actions/rootAction'

const SECTIONTITLE = styled.div`
  position: relative;  
  margin-top: -5px;
  &:before{
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 1px;
    background-image: linear-gradient(to right, transparent 80%, #818182 15%);
    background-size: 10px 100%;
    background-repeat: repeat-x;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  h3{    
    text-transform: uppercase;
    position: relative;
    display: inline-block;   
    background-color: ${Styles.white} ;
  }
`;

class SectionTitle extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      title: props.title
    }
  }
  render(){
    return (                    
      <SECTIONTITLE style={this.props.style}>
        {this.state.title != null ? <Styles.H3>{this.state.title}</Styles.H3> : null } 
      </SECTIONTITLE>                              
    );
  }
}

const mapStateToProps = (state) => {
  const { app } = state
  return { app }
}

const mapDispatchToProps = dispatch => (
  bindActionCreators({
    openSideMenu
  }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(SectionTitle)