import React from 'react';
import * as Styles from './Styled'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid';

//redux
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux'
import { openSideMenu } from '../config/actions/rootAction'


const IMGCONTAINER = styled.div`
  display: block;
  height: ${props => props.imgContainerHeight != null ? props.imgContainerHeight + 'px' : '100%'};
  position: relative;
  margin-bottom: 0;
  overflow: hidden;
  &:hover > img{
    filter: grayscale(100%);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
    transform: scale(1.1);
  }
  img{
    object-fit: cover;
    height: 100%;
    width: 100%;    
    -webkit-transition: all 0.4s linear;
    -moz-transition: all 0.4s linear;
    -ms-transition: all 0.4s linear;
    -o-transition: all 0.4s linear;
    transition: all 0.4s linear;  
  }
`;

const MENU = styled.div`
  padding: ${props=>props.shadow ? '10px' : '0px'};
  box-shadow: ${props=>props.shadow ? '1px 1px 10px 0px rgb(0 0 0 / 15%)' : 'none'};
  a{
    text-decoration: none;
  }
  h3{
    margin-bottom: 5px;
    margin-top: 10px; 
    line-height: 1.5;
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  ul{
    list-style: none;
    margin: ${props => props.type == "secondary" ? "15px 0 0 0" : 0};
    padding: 0;
    display: inline-block;
    font-size: 11px;
    color: #a1a4a6;
    font-weight: 700;
    position: relative;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    li{
      text-transform: none
    }
  }
  p{
    color: #a1a4a6;
    line-height: 1.5;
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
`;

class MenuComponent extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      src: props.src,
      name: props.name,
      meta: props.meta,
      price: props.price,      
    }
  }
  render() {

    return (
      <MENU style={this.props.style} shadow={this.props.shadow}>
        <IMGCONTAINER imgContainerHeight={this.props.imgContainerHeight}>
          <img src={process.env.REACT_APP_API_URL + '/resources' + this.state.src} style={this.props.imgStyle} />
        </IMGCONTAINER>
        <div>
          <Styles.H3>
            {this.state.name}
          </Styles.H3>
          <ul>
            {/* <li>{this.state.meta.date}</li> */}
            <li>Rp {this.state.price.toString().replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1,")},-</li>
          </ul>
        </div>
      </MENU>
    );
  }
}

const mapStateToProps = (state) => {
  const { app } = state
  return { app }
}

const mapDispatchToProps = dispatch => (
  bindActionCreators({
    openSideMenu
  }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(MenuComponent)