class WidgetSocial extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      title: props.title
    }
  }
  render(){
    return (                    
      <section style={{marginTop: 20}}>
        <WIDGETTRENDING>
          <h3>Trending</h3>
          <div>
            {list.map((value, index)=>{
              return(
                <WIDGETTRENDINGDATA key={index} style={index == list.length-1 ? {marginBottom: 0} : null}>
                  <a href={"/post/" + this.state.url}>
                    <img src={process.env.PUBLIC_URL + '/assets/img/' + value.src} />
                    <div>
                      <h3>{value.title}</h3>
                      <span>{value.meta.date}</span>
                    </div>
                  </a>
                </WIDGETTRENDINGDATA>
              );
            })}            
          </div>
        </WIDGETTRENDING>
        <WIDGETSOCIAL>        
          <div>
            <a href={"https://www.facebook.com/" + this.props.app.facebook} title="">
              <FacebookIcon htmlColor={Styles.grey}/>
            </a>
            <Styles.H4>3,553 </Styles.H4>
            <span>Followers</span>
          </div>
          <div>
            <a href={"https://www.instagram.com/" + this.props.app.instagram} title="" >
              <InstagramIcon htmlColor={Styles.grey}/>
            </a>
            <Styles.H4>11,211 </Styles.H4>
            <span>Followers</span>
          </div>
          <div>
            <a href="#" title="" >
              <YouTubeIcon htmlColor={Styles.grey}/>
            </a>
            <Styles.H4>9,328 </Styles.H4>
            <span>Followers</span>
          </div>        
        </WIDGETSOCIAL>         
      </section>                          
    );
  }
}

const mapStateToProps = (state) => {
  const { app } = state
  return { app }
}

const mapDispatchToProps = dispatch => (
  bindActionCreators({
    openSideMenu
  }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(WidgetSocial)