class Home extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      recentPostList: [],
      menuList: [],
    }
  }

  initial = async () =>{

    this.props.setLoading(true)

    //recent post
    await axios.get(process.env.REACT_APP_API_URL + '/post?limit=4').then((res)=>{      
      this.setState({
        recentPostList: res.data.data
      })
    })
    //menu
    await axios.get(process.env.REACT_APP_API_URL + '/menu?limit=4').then((res)=>{      
      this.setState({
        menuList: res.data.data
      })
    })

    this.props.setLoading(false) 
  }

  componentDidMount = () =>{
    this.initial()
  }


  render() {
    return (
      <Styles.Container>
        <LOGO>
          <Styles.H1>{this.props.app.appName}</Styles.H1>
        </LOGO>
        <BANNER>
          <Grid container spacing={1} >
            <Grid item xs={12} md={8} lg={8} xl={8}>
              <BLOGIMGBIG>
                <img src={process.env.PUBLIC_URL + '/assets/img/item1_web.jpg'} />
                <BLOGINFO>
                  <H3MAIN>
                    <a>Claire Foy on Becoming Mother Trading Her Crown for a Dragon Tattoo</a>
                  </H3MAIN>
                  <STATMAIN>
                    <ul>
                      <li>December 22, 2020</li>
                    </ul>
                  </STATMAIN>
                </BLOGINFO>
              </BLOGIMGBIG>
            </Grid>
            <Grid item xs={12} md={4} lg={4} xl={4}>
              <Grid container spacing={1} >
                <Grid item xs={12} md={6} lg={12} style={{
                  paddingBottom: 0
                }}>
                  <BLOGIMGSMALL>
                    <img src={process.env.PUBLIC_URL + '/assets/img/item1_web.jpg'} />
                    <BLOGINFO>
                      <Styles.H3>
                        <a>Let Emily Ratakowski Explain
                       Single Payer Health </a>
                      </Styles.H3>
                    </BLOGINFO>
                  </BLOGIMGSMALL>
                </Grid>
                <Grid item xs={12} md={6} lg={12} style={{
                  paddingTop: 8.5
                }}>
                  <BLOGIMGSMALL >
                    <img src={process.env.PUBLIC_URL + '/assets/img/item1_web.jpg'} />
                    <BLOGINFO>
                      <Styles.H3>
                        <a>Let Emily Ratakowski Explain
                       Single Payer Health </a>
                      </Styles.H3>
                    </BLOGINFO>
                  </BLOGIMGSMALL>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </BANNER>
        <SectionTitle title="OUR MENU" />
        <RECENTPOST>
          <Grid container spacing={1} >
            {!this.props.app.isLoading ? this.state.menuList.map((value, index) => {
              return (
                <Grid item xs={12} md={6} lg={3} xl={3} key={index}> 
                  <MenuComponent src={value.img} price={value.price} name={value.name} meta={value.meta} style={{ marginBottom: 10}} 
                  imgContainerHeight={222} />
                </Grid>
              );
            }) : null}
          </Grid>
        </RECENTPOST>
        <Grid container style={{ marginTop: 20 }}>
          <RPCONTAINER item xs={12} md={12} lg={9} xl={9}>
            <SectionTitle title="RECENT POST" />
            <Grid container spacing={3} >
              {this.state.recentPostList.map((value, index) => {
                if (index == 0) {
                  return (
                    <Grid item xs={12} md={12} lg={8} xl={8} key={index}>
                      <Postingan url={value.url} src={value.img} 
                        title={value.title} meta={value.meta} type="secondary" desc={value.description} 
                        imgContainerHeight={400} />
                    </Grid>
                  );
                }
              })}             
              <Grid item xs={12} md={12} lg={4} xl={4}>
                <Grid container spacing={1} >
                  {this.state.recentPostList.map((value, index) => {
                    if (index > 0 && index < 3) {
                      return (
                        <Grid item xs={12} md={6} lg={12} xl={12} key={index}>
                          <Postingan url={value.url} src={value.img} title={value.title} meta={value.meta} key={index} style={{ marginBottom: 20 }} 
                          imgContainerHeight={222} />
                        </Grid>
                      );
                    }
                  })}
                </Grid>
              </Grid>
            </Grid>
            <SectionTitle style={{ marginTop: 5 }} />
          </RPCONTAINER>
          <Grid item xs={12} md={12} lg={3} xl={3}>
            <WidgetSocial >

            </WidgetSocial>
          </Grid>
        </Grid>

      </Styles.Container>
    );
  }
}

const mapStateToProps = (state) => {
  const { app } = state
  return { app }
}

const mapDispatchToProps = dispatch => (
  bindActionCreators({
    openSideMenu,
    setLoading
  }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(Home)