import React from 'react';
import * as Styles from './Styled'
import styled from 'styled-components'
import DehazeIcon from '@material-ui/icons/Dehaze';

//redux
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux'
import { openSideMenu } from '../config/actions/rootAction'


const UL = styled.ul`
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  list-style: none;
  li{
    &:first-child{
      padding-left: 0;
    }
    display: inline-block;
    padding: 19px 11px 20px 11px;
    position: relative;    
    a{
      display: inline-block;
      color: #1a191e;
      font-size: 11px;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.6px;
      font-family: Poppins;
      text-decoration: none;
      background-color: transparent;
    }
  }
`;

const LI = styled.li`
  border-bottom: ${props => props.isCurrentUrl ? "solid 3px #58391c" : 'none'};
`;

const SIDEMENULISTA = styled.a`
  color: ${props => props.isCurrentUrl ? "#58391c!important" : '#fff!important'};
`;

const TOPBAR = styled.nav`
  width: 100%;
  border-bottom: 1px solid #181819;
  position: relative;
`;

const HEADER = styled(Styles.Container)`
  position: sticky;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  background: ${Styles.white};
`;

const HEADERMOBILE = styled(Styles.Container)`
  position: sticky;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  background: ${Styles.white};
  box-shadow: 1px 0px 3px 1px rgba(0,0,0,0.1);  
  @media (max-width: ${Styles.phoneWidth}) {
    padding-top: 15px!important;
    padding-bottom: 15px!important;
  }
`;

const HEADERMOBILETITLE = styled.div`  
  display: flex;
  align-items: center;  
  button{
    outline: none;
    border: none;
    background-color: transparent; 
    i{
      font-size: 20px;
    }          
  }
  h3{
    text-transform: uppercase;
    flex: 1;
    text-align: center;
    margin: 0;
    font-weight: 900;
  } 
`;

const SIDEMENU = styled.div`
  position: absolute;
  top: 52px;
  bottom: 0;
  right: 0;
  left: 0;
  width: ${props => props.open ? "100vw" : 0};
  height: 100vh;
  z-index: 1000;
  background-color: rgba(0,0,0,0.9);	
  overflow-x: hidden;
  transition: 0.5s;
`;

const SIDEMENULIST = styled.div`
  display: block;        
  /* padding: 40px; */
  margin-top: 50px;    
  a{    
    display: block;
    text-decoration: none;
    color: ${Styles.white};
    padding: 20px 0;
    font-family: Poppins;
    font-size: 17px;
    line-height: 25px;
    font-weight: 700;
    text-align: center;
  }
`;

const urlRoute = [
  {
    url: '/home',
    aliasUrl: '/',
    name: 'Home'
  },
  {
    url: '/post',
    name: 'Post'
  },
  {
    url: '/menu',
    name: 'Menu'
  },
  {
    url: '/about',
    name: 'About'
  }
];

class Header extends React.Component{

  constructor() {
    super();
    this.state = {
      width: window.innerWidth,      
    };
  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);      
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }

  handleWindowSizeChange = async () => {     
    if(window.innerWidth > 500){
      await this.props.openSideMenu(false); 
      this.reRenderBodyStyle()            
    }
    this.setState({ width: window.innerWidth });
  };

  sideMenu = async () => {
    await this.props.openSideMenu(!this.props.app.isSideMenuOpen)    
    this.reRenderBodyStyle()       
  }

  reRenderBodyStyle = ()=>{
    if(this.props.app.isSideMenuOpen){
      document.body.style.margin = 0;    
      document.body.style.overflow= 'hidden';
      document.body.style.height = 100;          
    }else{
      document.body.style = {}
    } 
  }

  render(){    
    const { width } = this.state;
    const isMobile = width <= 500;    
    if (isMobile) {
      return (
        <HEADERMOBILE className="haha">
          <HEADERMOBILETITLE>
            <button onClick={this.sideMenu}>
              {/* <DehazeIcon/> */}
              <i className="fas fa-bars"></i>
            </button>
            <h3>Kopistasi</h3>
            <button onClick={this.sideMenu}>              
              <i className="fas fa-search"></i>
            </button>
          </HEADERMOBILETITLE>          
          <SIDEMENU open={this.props.app.isSideMenuOpen} >
            <SIDEMENULIST>
              {urlRoute.map((value, index)=>{
                return(<SIDEMENULISTA key={index} isCurrentUrl={window.location.pathname == value.url} href={value.url} >{value.name}</SIDEMENULISTA>);
              })}                                          
            </SIDEMENULIST>
          </SIDEMENU>
        </HEADERMOBILE>
      );
    }else{      
      return (
        //header
        <HEADER>
          <TOPBAR>
            <UL>
              {urlRoute.map((value, index)=>{
                return(
                  <LI key={index} isCurrentUrl={window.location.pathname == value.url || window.location.pathname == value.aliasUrl}><a href={value.url} >{value.name}</a></LI>
                );
              })}                            
            </UL>            
            {/* <button onClick={this.sideMenu}>              
              <i className="fas fa-search"></i>
            </button>           */}
          </TOPBAR>
          
        </HEADER>
      );
    }
  }
}

const mapStateToProps = (state) => {
  const { app } = state
  return { app }
}

const mapDispatchToProps = dispatch => (
  bindActionCreators({
    openSideMenu,
  }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(Header)