const MENU = styled.div`

`;

class Menu extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      menuList: [],
    }
  }

  initial = async () =>{

    this.props.setLoading(true)

    //menu
    await axios.get(process.env.REACT_APP_API_URL + '/menu').then((res)=>{            
      this.setState({
        menuList: res.data.data
      })
    })

    this.props.setLoading(false) 
  }

  componentDidMount = () =>{
    this.initial()
  }


  render() {
    return (
      <Styles.Container>         
        <Grid container style={{ marginTop: 20}}>
          <Grid item xs={12} md={12} lg={9} xl={9}>
            <MENUCONTAINER container spacing={2}>
            {!this.props.app.isLoading ? this.state.menuList.map((value, index) => {
              return (
                <Grid item xs={12} md={6} lg={4} xl={4} key={index}> 
                  <MenuComponent src={value.img} name={value.name} meta={value.meta} price={value.price} style={{ marginBottom: 10, }} 
                  shadow={false} imgContainerHeight={222} />
                </Grid>
              );
            }) : null}
            </MENUCONTAINER>
          </Grid>          
          <Grid item xs={12} md={12} lg={3} xl={3}>
            <WidgetSocial />
          </Grid>
        </Grid>
      </Styles.Container>
    );
  }
}

const mapStateToProps = (state) => {
  const { app } = state
  return { app }
}

const mapDispatchToProps = dispatch => (
  bindActionCreators({
    openSideMenu,
    setLoading
  }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(Menu)