import React from 'react';
import * as Styles from './Styled'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid';

//redux
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux'
import { openSideMenu } from '../config/actions/rootAction'


const IMGCONTAINER = styled.div`
  display: block;
  height: ${props => props.imgContainerHeight != null ? props.imgContainerHeight + 'px' : '100%'};
  position: relative;
  margin-bottom: 0;
  overflow: hidden;
  &:hover > img{
    filter: grayscale(100%);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
    transform: scale(1.1);
  }
  img{
    object-fit: cover;
    height: 100%;
    width: 100%;    
    -webkit-transition: all 0.4s linear;
    -moz-transition: all 0.4s linear;
    -ms-transition: all 0.4s linear;
    -o-transition: all 0.4s linear;
    transition: all 0.4s linear;  
  }
`;

const POSTINGAN = styled.div`
  a{
    text-decoration: none;
  }
  h3{
    margin-bottom: 5px;
    margin-top: 10px; 
    line-height: 1.5;
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  ul{
    list-style: none;
    margin: ${props => props.type == "secondary" ? "15px 0 0 0" : 0};
    padding: 0;
    display: inline-block;
    font-size: 11px;
    color: #a1a4a6;
    font-weight: 700;
    position: relative;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    li{

    }
  }
  p{
    color: #a1a4a6;
    line-height: 1.5;
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
`;

const POSTLIST = styled.div`  
  margin-bottom: 15px;
  a{    
    margin-bottom: 15px;
    text-decoration: none;
  }
  img{    
    width: 100%;
    float: left;
    margin-bottom: 0;
    overflow: hidden;
    -webkit-transition: all 0.4s linear;
    -moz-transition: all 0.4s linear;
    -ms-transition: all 0.4s linear;
    -o-transition: all 0.4s linear;
    transition: all 0.4s linear;  
    &:hover{
      filter: grayscale(100%);
      -webkit-transform: scale(1.1);
      -moz-transform: scale(1.1);
      -ms-transform: scale(1.1);
      -o-transform: scale(1.1);
      transform: scale(1.1);
    }
  }  
  div{    
    h3{
      font-size: 15px;      
      padding-right: 80px;    
      margin: auto;
      letter-spacing: -0.2px;
      color: #1a191e;
      font-weight: 900;      
      white-space: normal;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      @media (max-width: ${Styles.phoneWidth}) {
        padding-right: 0;
      }
    }
    span{
      display: inline-block;
      color: #a1a4a6;
      font-size: 11px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      &:first-child{
        color: #007cbd;
      }
    }
  }
`;

const POSTLISTINFO = styled(Grid)`
  display: flex;
  align-items: center;
`;

class Postingan extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      src: props.src,
      title: props.title,
      meta: props.meta,
      desc: props.desc,
      url: props.url,
    }
  }
  render() {
    if (this.props.type == 'secondary') {
      return (
        <POSTINGAN style={this.props.style} type={this.props.type}>
          <IMGCONTAINER imgContainerHeight={this.props.imgContainerHeight}>
            <img src={process.env.REACT_APP_API_URL + '/resources' + this.state.src}  style={this.props.imgStyle} />
          </IMGCONTAINER>
          <div>
            <ul>
              <li>{this.state.meta.date}</li>
            </ul>
            <a href={"/post/" + this.state.url}>
              <Styles.H3>
                {this.state.title}
              </Styles.H3>
              <Styles.P>
                {this.state.desc}
              </Styles.P>
            </a>
          </div>
        </POSTINGAN>
      );
    } else if(this.props.type == 'list'){
      return (
        <POSTLIST style={this.props.style}>
          <a href={"/post/" + this.state.url}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={12} lg={3} xl={3}>
                <IMGCONTAINER >
                  <img src={process.env.REACT_APP_API_URL + '/resources' + this.state.src}  style={this.props.imgStyle} />
                </IMGCONTAINER>              
              </Grid>
              <POSTLISTINFO item xs={12} md={12} lg={9} xl={9}>
                <div>                  
                  <h3>{this.state.title}</h3>
                  <span>{this.state.meta.date}</span>
                </div>
              </POSTLISTINFO>
            </Grid>
          </a>
        </POSTLIST>
      );
    } else {
      return (
        <POSTINGAN style={this.props.style}>
          <IMGCONTAINER imgContainerHeight={this.props.imgContainerHeight}>
            <img src={process.env.REACT_APP_API_URL + '/resources' + this.state.src}  style={this.props.imgStyle} />
          </IMGCONTAINER>
          <div>
            <a href={"/post/" + this.state.url}>
              <Styles.H3>
                {this.state.title}
              </Styles.H3>
            </a>
            <ul>
              <li>{this.state.meta.date}</li>
            </ul>
          </div>
        </POSTINGAN>
      );
    }
  }
}

const mapStateToProps = (state) => {
  const { app } = state
  return { app }
}

const mapDispatchToProps = dispatch => (
  bindActionCreators({
    openSideMenu
  }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(Postingan)