class PostDetail extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      postData: null,
      topPostList: [],
    }
  }

  initial = async () =>{

    this.props.setLoading(true)

    //recent post    
    await axios.get(process.env.REACT_APP_API_URL + '/post/' + this.props.match.params.url).then((res)=>{                  
      this.setState({
        postData: res.data.data
      })      
    })    

    //recent post
    await axios.get(process.env.REACT_APP_API_URL + '/post?limit=3').then((res)=>{      
      this.setState({
        topPostList: res.data.data
      })
    })

    this.props.setLoading(false) 
  }

  componentDidMount = () =>{
    this.initial()
  }

  render() {    
    return (
      <Styles.Container>        
        <Grid container style={{ marginTop: 20}}>
          <RPCONTAINER item xs={12} md={12} lg={9} xl={9}>
            {!this.props.app.isLoading && this.state.postData!=null? 
              <POSTINGAN>
                <ul>
                  <li>{this.state.postData.meta.date}</li>
                </ul>
                <Styles.H3>
                  {this.state.postData.title}
                </Styles.H3>
                <IMGCONTAINER>
                  <img src={process.env.REACT_APP_API_URL + '/resources' + this.state.postData.img}  />
                </IMGCONTAINER>
                {/* <div dangerouslySetInnerHTML={{__html: this.state.postData.description}} />                                  */}
                <Styles.P>
                  {this.state.postData.description}
                </Styles.P>
              </POSTINGAN>
            : null}            
            <POSTCONTROL>
              <ul>
                <li >
                  <a href="#" title=""><span>Make More Time in Motherhood &amp; Your Career</span></a>
                </li>
                <li>
                  <a href="#" title="">
                    <span>5 Simple Steps To A Happier and Healthier You</span></a>                  
                </li>
              </ul>
            </POSTCONTROL>
            <SectionTitle title="TOP POST" />
            <RECENTPOST>
              <Grid container spacing={1} >
                {this.state.topPostList.map((value, index) => {
                  return (
                    <Grid item xs={12} md={6} lg={4} xl={4} key={index}>
                      <Postingan src={value.img} url={value.url} title={value.title} meta={value.meta} style={{ marginBottom: 10 }} 
                      imgContainerHeight={222} />
                    </Grid>
                  );
                })}
              </Grid>
            </RECENTPOST>
          </RPCONTAINER>          
          <Grid item xs={12} md={12} lg={3} xl={3}>
            <WidgetSocial />
          </Grid>
        </Grid>

      </Styles.Container>
    );
  }
}

const mapStateToProps = (state) => {
  const { app } = state
  return { app }
}

const mapDispatchToProps = dispatch => (
  bindActionCreators({
    openSideMenu,
    setLoading
  }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(PostDetail)