import React from 'react';
import * as Styles from '../components/Styled'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid';
import Postingan from '../components/Postingan'
import SectionTitle from '../components/SectionTitle'

//redux
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux'
import { openSideMenu, setLoading } from '../config/actions/rootAction'
import WidgetSocial from '../components/WidgetSocial';
//url
import axios from 'axios'

const MENUCONTAINER = styled(Grid)`
  padding-right: 30px;
  @media (max-width: ${Styles.phoneWidth}) {
    padding-right: 0px;
  }
  @media (max-width: ${Styles.tabletWidth}) {
    padding-right: 0px;
  }
`;

const LOADMORE = styled.div`
  width: 100%;
  text-align: center;
  margin: 20px 0;
  button{
    cursor: pointer;
    border: none;
    outline: none;
    padding: 10px 15px;    
    font-weight: 700;
    font-size: 13px;
  }
`;

class Post extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      postList: [],
      postListLength: 5,
    }
  }

  initial = async () =>{

    this.props.setLoading(true)

    //menu
    await axios.get(process.env.REACT_APP_API_URL + '/post?limit=100').then((res)=>{            
      this.setState({
        postList: res.data.data
      })
    })

    this.props.setLoading(false) 
  }

  componentDidMount = () =>{
    this.initial()
  }

  loadMore = () =>{
    this.setState({
      postListLength: this.state.postListLength + 5
    })    
  }

  render() {
    return (
      <Styles.Container>         
        <Grid container style={{ marginTop: 20}}>
          <Grid item xs={12} md={12} lg={9} xl={9}>
            <MENUCONTAINER container spacing={2}>
            {!this.props.app.isLoading ? this.state.postList.slice(0, this.state.postListLength).map((value, index) => {
              return (
                <Grid item xs={12} md={12} lg={12} xl={12} key={index}> 
                  <Postingan src={value.img} title={value.title} meta={value.meta} url={value.url} type={'list'}
                  imgContainerHeight={222} />
                </Grid>
              );
            }) : null}
            </MENUCONTAINER>
            {this.state.postListLength < this.state.postList.length ? 
            <LOADMORE>
              <button onClick={this.loadMore}>Load More</button>
            </LOADMORE>
            : null }
          </Grid>          
          <Grid item xs={12} md={12} lg={3} xl={3}>
            <WidgetSocial />
          </Grid>
        </Grid>
      </Styles.Container>
    );
  }
}

const mapStateToProps = (state) => {
  const { app } = state
  return { app }
}

const mapDispatchToProps = dispatch => (
  bindActionCreators({
    openSideMenu,
    setLoading
  }, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(Post)